.tools-wrapper {
  display: inline-flex;
  margin: 120px 0 0 35px;
  flex-direction: column;
}

.tools {
  position: relative;
  display: inline-flex;
  flex-direction: column;
}

.tools__item {
  display: inline-flex;
  list-style: none;
  margin-bottom: 3px;
  width: 55px;
  height: 55px;
}

.tools__item button {
  background-color: #212121;
  width: 55px;
  height: 55px;
  padding: 0;
  border: 2px solid #444;
  border-radius: 20%;
  font: inherit;
  color: inherit;
  box-sizing: border-box;
  outline: none;
  background-repeat: no-repeat;
  background-position: center;
}

.tools__item:hover button {
  background-color: #313131;
}

.fill-color-action {
  background-image: url("./img/fillColor.png");
  background-size: 40px;
}

.draw-action {
  background: url("./img/pencil.png") no-repeat center;
}

.choose-color-action {
  background: url("./img/chooseColor.png") no-repeat center;
}

.erase-action {
  background: url("./img/eraser.png") no-repeat center;
}

.tools__item--active button {
  border: 2px solid gold;
  border-radius: 20%;
}

.color-palette-wrapper {
  display: flex;
  position: relative;
  width: 90px;
  height: 90px;
}

.color-palette__primary {
  top: 0;
  left: 0;
  position: absolute;
  z-index: 1;
}

.color-palette__secondary {
  top: 35px;
  right: 0;
  position: absolute;
  z-index: 0;
}

.color-change {
  width: 24px;
  height: 24px;
  position: absolute;
  bottom: 6px;
  left: 6px;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  box-sizing: border-box;
  outline: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('./img/colorChange.png');
  transition: transform 200ms ease-in-out;
  transform: rotate(-10deg);
}

.color-change:hover {
  transform: rotate(-190deg);
}
