.color-palette {
  width: 55px;
  height: 55px;
  border-radius: 20%;
  border: 2px solid #444;
  position: relative;
  padding: 0;
  outline: none;
}

.color-palette:hover {
  border-color: #6d6d6d;
}

.color-palette::-webkit-color-swatch-wrapper {
  padding: 0;
}

.color-palette::-webkit-color-swatch {
  border: none;
}
