.frame {
  width: 110px;
  height: 110px;
  box-sizing: border-box;
  position: relative;
  background-color: #dbdbdb;
  margin-bottom: 7px;
  border-radius: 5%;
  border: 2px solid transparent;
}

.frame button {
  position: absolute;
  width: 28px;
  height: 28px;
  padding: 0;
  display: none;
  border: none;
  cursor: pointer;
  background-color: #dbdbdb;
}

.frame .add-to-animation-action {
  display: block;
  background-color: dimgray;
  border-bottom-right-radius: 5px;
  border-top-left-radius: 5px;
  font-size: 20px;
}

.frame:hover button {
  display: block;
}

.frame:hover .add-to-animation-action,
.frame--active .add-to-animation-action {
  background-color: gold;
}

.frame:hover,
.frame--active {
  border: 2px solid gold;
}

.frame__img {
  width: 110px;
  height: 110px;
}

.delete-action {
  top: 0;
  right: 0;
  background: url("./img/trash.png") no-repeat center;
}

.copy-action {
  bottom: 0;
  right: 0;
  background: url("./img/copy.png") no-repeat center;
}

.add-to-animation-action {
  top: 0;
  left: 0;
}
