.preview {
  width: 300px;
  height: 300px;
  position: relative;
  margin: 5px auto;
  background-color: #dbdbdb;
  border-radius: 2%;
}

.fullscreen-preview {
  display: flex;
  justify-content: center;
  align-items: center;
}

.frame__img-active {
  width: 300px;
  height: 300px;
}

.full-screen-action {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  padding: 0;
  display: none;
  border: none;
  cursor: pointer;
  background: url("./img/fullScreen.png");
}

.preview:hover button {
  display: block;
}

.emptyFrame {
  width: 300px;
  height: 300px;
  background-color: #dbdbdb;
  border-radius: 2%;
}
