::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #666;
  cursor: pointer;
}

::-webkit-scrollbar-track {
  background-color: rgba(50, 50, 50, 0.4);
}

.frame-list-wrapper {
  overflow-y: auto;
  padding-right: 5px;
}

.frame-list {
  display: flex;
  flex-direction: column;
  margin: 5px 0 0 35px;
}

.add-frame-button {
  width: 110px;
  height: 50px;
  border-radius: 7%;
  padding: 0;
  border: 2px solid #dbdbdb;
  font-size: 16px;
  color: #dbdbdb;
  background-color: #212121;
  word-wrap: break-word;
  box-sizing: border-box;
  cursor: pointer;
}

