.wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

header {
  height: 50px;
  background-color: #333;
  border-bottom: 2px solid #dbdbdb;
  text-align: center;
  font-size: 35px;
  color: #dbdbdb;
  line-height: 50px;
  box-sizing: border-box;
}

main {
  display: flex;
  width: 100%;
  height: calc(100% - 50px);
}

.content {
  width: 800px;
  background-color: #dbdbdb;
}

.sidebar-left,
.sidebar-right {
  width: 400px;
  background-color: #212121;
  /* border-top: 2px solid #354F51; */
}

.sidebar-left{
  display: flex;
}
